import React from 'react';
import { 
    ExpansionPanel,
    ExpansionPanelSummary,
    ExpansionPanelDetails,
    Typography,
    Avatar,
    makeStyles
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import OsInfoList from './osInfoList';

const useOsInfoPanelStyles = makeStyles(({ spacing }) => ({
    expansionDetailsWrapper: {
        padding: 0
    },
    panelTextImg: {
        display: 'flex',
        justifyContent: 'space-evenly',
        alignItems: 'center'
    }
}))

export default ({ osInfo }) => {
    const { expansionDetailsWrapper, panelTextImg } = useOsInfoPanelStyles();

    return (
        <ExpansionPanel>
            <ExpansionPanelSummary
                expandIcon={ <ExpandMoreIcon /> }
                aria-controls={`${osInfo.osId}-os-info-content`}
                id={`${osInfo.osId}-os-info-header`}
                classes={{
                    content: panelTextImg
                }}
            >
                {osInfo.osLogo &&
                    <Avatar alt={ osInfo.logoAlt } src={ osInfo.osLogo } />
                }
                <Typography gutterBottom variant="subtitle1" align='center'>
                    <strong>{osInfo.osName}</strong>
                </Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails
                id={`${osInfo.osId}-os-info-content`}
                className={ expansionDetailsWrapper }
            >
                <OsInfoList osDetails={ osInfo }  />
            </ExpansionPanelDetails>
        </ExpansionPanel>
    );
}