import React from 'react';
import { List, ListItem, ListItemText, Divider, makeStyles, Typography } from '@material-ui/core';

const osInfoListStyles = makeStyles(({ spacing }) => ({
    listWrapper: {
        width: '100%'
    },
    liStyles: {
        display: 'flex',
        justifyContent: 'space-between'
    }
}));

export default ({ osDetails: 
    { osId, osName, minBrowserVersion, minOsVersion, behindFlag } 
}) => {

    const { listWrapper, liStyles } = osInfoListStyles();

    return (
        <List className={ listWrapper }>
            <Divider />
            <ListItem divider className={ liStyles }>
                <Typography variant='body1' component='p' color='textSecondary'>
                    Browser version: 
                </Typography>
                <Typography variant='body1' component='p' color='textSecondary'>
                    {`>=${minBrowserVersion}`}
                </Typography>
            </ListItem>
        </List>
    );
}